  align-items: center;
  justify-content: center;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.grid-item span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.grid-item:hover {
  background-color: #f0f0f0;
}

.grid-item.answered {
  background-color: #ebf5fb;
}

