/* Import Nunito font */

/* Color variables */
:root {
    --deep-navy: #0A2342;
    --dull-gold: #C6A77B;
    --off-white: #F5F5F5;
    --steel-grey: #9EA3B0;
}

/* Reset and base styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Nunito', sans-serif;
    line-height: 1.6;
    color: var(--deep-navy);
    background-color: var(--off-white);
}

/* Header and navigation */
header {
    background-color: var(--deep-navy);
    padding: 1rem 0;
    width: 100%;
}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 2rem;
}

.logo img {
    height: 50px;
}

.main-nav {
    display: flex;
    list-style: none;
}

.main-nav li {
    margin-left: 2rem;
}

.main-nav li a {
    text-decoration: none;
    color: var(--off-white);
    font-weight: 600;
    transition: color 0.3s ease;
}

.main-nav li a:hover {
    color: var(--dull-gold);
}

/* Main content */
main {
    width: 100%;
    min-height: calc(100vh - 200px); /* Adjust based on header and footer height */
}

.content-wrapper {
    max-width: 1200px;
    margin: 0 auto;
    padding: 2rem;
}

section {
    margin-bottom: 4rem;
}

h1, h2, h3 {
    margin-bottom: 1rem;
    font-weight: 700;
    color: var(--deep-navy);
}

/* Hero section */
#hero {
    background-color: var(--deep-navy);
    color: var(--off-white);
    padding: 4rem 0;
    width: 100%;
}

.hero-content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.hero-text {
    flex: 1 1;
}

#hero h1 {
    font-size: 2.5rem;
    color: var(--off-white);
    margin-bottom: 1rem;
}

#hero p {
    font-size: 1.2rem;
    margin-bottom: 2rem;
}

/* Login section */
.login-section {
    background-color: var(--off-white);
    padding: 2rem;
    border-radius: 8px;
    width: 300px;
}

#login-form {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}

#login-form input {
    padding: 0.5rem;
    border: 1px solid var(--steel-grey);
    border-radius: 4px;
}

#login-button {
    width: 100%;
}

#forgot-password, #create-account {
    display: block;
    text-align: center;
    margin-top: 0.5rem;
    color: var(--deep-navy);
    text-decoration: none;
}

.social-login {
    display: flex;
    justify-content: space-between;
    margin-top: 1rem;
}

.btn-social {
    flex: 1 1;
    padding: 0.5rem;
    margin: 0 0.25rem;
    background-color: var(--steel-grey);
    color: var(--deep-navy);
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.btn-social:hover {
    background-color: var(--dull-gold);
}

