#features {
    padding: 4rem 5%; /* Use percentage for responsive side padding */
    max-width: 1200px; /* Max width to prevent stretching on very wide screens */
    margin: 0 auto; /* Center the container */
}

.feature-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 2rem;
    gap: 2rem;
    justify-content: center; /* Center grid items horizontally */
}

.feature-card {
    background-color: #fff;
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
}

.feature-card h3 {
    color: var(--dull-gold);
    margin-bottom: 1rem;
}

/* Responsive adjustments */
@media (max-width: 768px) {
    #features {
        padding: 3rem 3%;
    }
    
    .feature-grid {
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
}

@media (max-width: 480px) {
    #features {
        padding: 2rem 2%;
    }
    
    .feature-grid {
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}

/* CTA section */
#cta {
    background-color: var(--dull-gold);
    color: var(--deep-navy);
    padding: 4rem 0;
    text-align: center;
    width: 100%;
}

.cta-content {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 2rem;
}

#cta h2 {
    color: var(--deep-navy);
}

#cta .btn-primary {
    background-color: var(--deep-navy);
    color: var(--off-white);
    display: inline-block;
    margin-top: 1rem;
}

#cta .btn-primary:hover {
    background-color: #0D2E5A;
}

/* Footer */
footer {
    background-color: var(--deep-navy);
    color: var(--off-white);
    padding: 2rem 0;
    width: 100%;
}

.footer-content {
    display: flex;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 2rem;
}

.footer-section {
    flex: 1 1;
    margin-right: 2rem;
}

.footer-section h3 {
    margin-bottom: 1rem;
    font-weight: 700;
    color: var(--dull-gold);
}

.footer-section ul {
    list-style: none;
}

.footer-section ul li {
    margin-bottom: 0.5rem;
}

.footer-section a {
    color: var(--off-white);
    text-decoration: none;
    transition: color 0.3s ease;
}

.footer-section a:hover {
    color: var(--dull-gold);
}

.social-icons {
    display: flex;
}

.social-icon {
    margin-right: 1rem;
}

.footer-bottom {
    text-align: center;
    margin-top: 2rem;
    padding-top: 1rem;
    border-top: 1px solid var(--steel-grey);
}

/* Button styles */
.btn {
    padding: 0.5rem 1rem;
    border-radius: 4px;
    text-decoration: none;
    font-weight: 600;
    transition: background-color 0.3s ease;
    white-space: nowrap;
    display: inline-block;
    cursor: pointer;
    border: none;
}

.btn-primary {
    background-color: var(--dull-gold);
    color: var(--deep-navy);
}

.btn-primary:hover {
    background-color: #B39164;
}

.btn-secondary {
    background-color: transparent;
    color: var(--off-white);
    border: 1px solid var(--off-white);
}

.btn-secondary:hover {
    background-color: rgba(255, 255, 255, 0.1);
}

/* Form styles */
.form-group {
    margin-bottom: 1rem;
}

.form-group label {
    display: block;
    margin-bottom: 0.5rem;
}

.form-group input,
.form-group select {
    width: 100%;
    padding: 0.5rem;
    border: 1px solid var(--steel-grey);
    border-radius: 4px;
}

/* Subscription page styles */
.subscription-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 2rem;
    gap: 2rem;
    margin: 2rem 0;
}

.subscription-card {
    background-color: #fff;
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.subscription-card h2 {
    color: var(--deep-navy);
}

.subscription-card .price {
    font-size: 1.5rem;
    font-weight: 700;
    color: var(--dull-gold);
    margin: 1rem 0;
}

.subscription-card ul {
    list-style: none;
    margin-bottom: 1rem;
}

.subscription-card .btn {
    width: 100%;
}

/* Responsive design */
@media (max-width: 768px) {
    nav {
        flex-direction: column;
        align-items: flex-start;
    }

    .main-nav {
        margin-top: 1rem;
        flex-direction: column;
    }

    .main-nav li {
        margin: 0.5rem 0;
    }

    .hero-content {
        flex-direction: column;
    }

    .login-section {
        width: 100%;
        margin-top: 2rem;
    }

    .footer-content {
        flex-direction: column;
    }

    .footer-section {
        margin-bottom: 2rem;
    }
}

/* Animations */
@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

.fade-in {
    animation: fadeIn 0.5s ease-in;
}

/* Accessibility */
.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
}

/* Focus styles for better keyboard navigation */
a:focus,
button:focus,
input:focus,
select:focus {
    outline: 2px solid var(--dull-gold);
    outline-offset: 2px;
}
.error {
    color: red;
    margin-bottom: 1rem;
  }
  
  button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
  .card {
    perspective: 1000px;
    width: 300px;
    height: 400px;
    margin: 20px auto;
  }
  
  .card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
  }
  
  .card-inner.flipped {
    transform: rotateY(180deg);
  }
  
  .card-front, .card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 10px;
  }
  
  .card-back {
    transform: rotateY(180deg);
  }
  
  .question-info {
    margin-top: 20px;
    font-size: 0.9em;
    color: #666;
  }
  
  button {
    margin-top: 10px;
    padding: 10px;
    cursor: pointer;
  }
